.tb_pop_overlay__ {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9998;
    opacity: 0;
    background-color: #000;
    transition: opacity .15s linear;
}

.tb_pop_overlay__.tb_show_02_ {
    opacity: .8;
}

.tb_upgrade_001 {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    overflow: hidden;
    outline: 0;
    opacity: 0;
    visibility: hidden;
    transition: all .300ms linear;
    display: none;
}

.tb_upgrade_001.tb_show_02_ {
    opacity: 1;
    overflow-x: hidden;
    overflow-y: auto;
    visibility: visible;
    display: block;
}

.tb_upgrade_001.tb_show_02_ .tb_popup_wrap_002 {
    -webkit-transform: none;
    transform: none;
    opacity: 1;
    visibility: visible;
}
.tb_popup_wrap_002 {
    -webkit-transition: -webkit-all 0.3s ease-out;
    transition: -webkit-all 0.3s ease-out;
    transition: all 0.3s ease-out;
    transition: all 0.3s ease-out, -webkit-all 0.3s ease-out;
    min-height: calc(100% - 3.5rem);
    max-width: 570px;
    margin: 2rem auto;
    padding: 0 15px;
    opacity: 0;
    visibility: hidden;
    display: flex;
    align-items: center;
    min-height: calc(100% - 4rem);
    position: relative;
    width: auto;
    pointer-events: none;
}

.tb_popup_wrap_in__ {
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    width: 100%;
    pointer-events: auto;
    background-color: #ffffff;
    background-clip: padding-box;
    border: 0 solid rgba(0, 0, 0, 0.2);
    border-radius: 0.3rem;
    -webkit-box-shadow: 0 0.5rem 1rem rgb(0 0 0 / 10%);
    box-shadow: 0 0.5rem 1rem hsl(0deg 0% 0% / 10%);
    outline: 0;
}

.tb_popup_body_ {
    position: relative;
    -webkit-box-flex: 1;
    -ms-flex: 1 1 auto;
    flex: 1 1 auto;
    padding: 1.75rem;
    text-align: center;
}

.tb_popup_img__003{
    max-width: 280px;
    margin: 25px auto;
}

.tb_pop_up_img__02 {
    max-width: 75%;
    display: block;
    margin: 0 auto;
}

.tb_pupup_content__003 {
    padding: 10px 0;
}
.tb_title_004 {
    font-size: 30px;
    margin-bottom: 15px;
    font-weight: 600;
}

.tb_pera__004 {
    font-size: 16px;
    color: #545454;
    line-height: 160%;
}

.tb_popup_btn_004{
    margin-top: 25px;
}

.tb_btn__005 {
    display: inline-block;
    font-weight: normal;
    color: #3F4254;
    text-align: center;
    vertical-align: middle;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: 0.60rem 1rem;
    font-size: 1rem;
    line-height: 1.5;
    transition: color 0.3s ease-in-out, background-color 0.3s ease-in-out, border-color 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
    color: #FFFFFF;
    text-decoration: none;
    border-radius: 3px;
    font-weight: 500;
    background-color: #FF4051;
    border-color: #FF4051;
}

.tb_btn__005:hover:not(.btn-text):not(:disabled):not(.disabled), .tb_btn__005:focus:not(.btn-text), .tb_btn__005.focus:not(.btn-text) {
    color: #FFFFFF;
    background-color: #e23442;
    border-color: #e23442;
    text-decoration: none;
}

@media screen and (max-width: 767px) {
.tb_title_004 {
